    padding-top: 60px;
    min-height: 100vh;
}

.background {
    width: 100vw;
}

.background-container {
    position: relative; 
    overflow: hidden;
}

.background {
    width: 100%;
    object-fit: cover; 
    background-attachment: fixed;
}

