    width: 24vw;
}

.curated-notes {
    width: 82%;
}

.cert {
    width: 90%;
}

.column {
    display: flex;
    justify-content: center;
    align-self: center;
}

.content-box {
    overflow-y: scroll;
}

.hi-fi {
    width: 100%;
